body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#setkey {
  display: flex;
  flex-direction: column;
  padding: 5px;
  margin: 5px;
}

#icon {
  padding: 5px;
}

#name {
  padding: 5px;
}

#key {
  padding: 5px;
}

button {
  margin: 5px;
  height: 30px;
}

#main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.setbut {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.wp {
  height: 50px;
  border: black;
  border-style: double;
}